@import "../styles/theme.scss";

.toolTip {
  display: inline-block;
  position: relative;

  & > .tooltipText {
    visibility: hidden;
    display: block;
    background-color: $primary;
    color: $text-black;
    border-radius: 8px;
    width: max-content;
    max-width: 360px;
    padding: 6px 8px;
    white-space: pre-wrap;

    font-size: 14px;
    font-family: $font-family;
    line-height: 20px;
    font-weight: $normal;
    letter-spacing: -0.04em;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    // bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 15px;
  }

  &:hover > .tooltipText {
    visibility: visible;
  }
}
